import React, {ReactNode} from 'react';

// import '@fontsource/roboto/300.css';
// import '@fontsource/roboto/400.css';
// import '@fontsource/roboto/500.css';
// import '@fontsource/roboto/700.css';
import {Button, Card, CardContent, CardHeader, CardMedia, Stack, Typography} from "@mui/material";
import PhoneIcon from '@mui/icons-material/Phone';
import MailIcon from '@mui/icons-material/Mail';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
const Helicon: string = require('./assets/helicon.png');
const Google: string = require('./assets/google.png');
const Tim: string = require('./assets/tim.png');

function Logo() {
    return <Typography variant="h4" gutterBottom>
        Consultim
      </Typography>
}

function Introduction() {
    return <div style={{
        paddingTop: '40px',
        marginBottom: '40px',
        paddingBottom: '50px',
        backgroundImage: `url(${Tim})`,
        backgroundPosition: '-130px 50%',
        backgroundSize: 'cover',
        backgroundRepeat: 'no-repeat',
    }}>
        <div style={{ position: 'relative', left: '40%', width: '60%' }}>
            <Typography variant="subtitle2">- SOLUTIONS ARCHITECT</Typography>
            <Typography variant="h5">Expand your possibilities by leveraging cloud architecture</Typography>
            <Typography variant="body2" style={{ marginTop: '20px' }}>
                Get help from Tim fverberg, an experienced software architect/developer
                who thrives on solving complex issues in a team environment.
            </Typography>
        </div>
    </div>
}

function PhoneButton() {
    return <Button variant="outlined" size="medium" startIcon={<PhoneIcon />} onClick={() => window.open('tel:+46707878721', '_self')}>+46-707 87 87 21</Button>;
}

function EmailButton() {
    return <Button variant="outlined"  size="medium" startIcon={<MailIcon />} style={{textTransform: 'none'}} onClick={() => window.open('mailto:info@consultim.se', '_self')}>info@consultim.se</Button>
}

function Contact() {
    return <div>
        <Typography variant="body1" style={{ marginBottom: '5px', marginLeft: '15px' }}>Contact information</Typography><Card sx={{ width: '100%' }}>
      <CardContent>
          <Stack spacing={1}>
              <div>
                  <Typography variant="body2">Tim fverberg</Typography>
                  <Typography variant="body2" gutterBottom>Solutions Architect / Software Engineer</Typography>
              </div>
              <PhoneButton />
              <EmailButton />
          </Stack>
      </CardContent>
    </Card>
    </div>
}

function LinkedIn() {
    // return <div>
    //     <LinkedInIcon sx={{ color: '#0077B5' }} fontSize="medium" className="linked-in" />
    // </div>;
    return <LinkedInIcon sx={{ color: '#0077B5', cursor: 'pointer' }} fontSize="medium" className="linked-in" onClick={() => window.open('https://linkedin.com/in/tim-fverberg')} />
}

function Copyright() {
    // return <Typography variant="caption" className="copyright" component="div">
    return <div>
        <Typography variant="caption" className="copyright">
        Copyright  { new Date().getFullYear() } Consultim
      </Typography>
    </div>
}


function BottomMenu(props: { children: ReactNode }) {
    return <Stack className="bottom-menu" alignItems="center" spacing={1}>
        {props.children}
    </Stack>
}

function Experience({title, date, img, link}: {title: string, date: string, img: string, link: string}) {
    return <Card sx={{ cursor: 'pointer' }} onClick={() => window.open(link)}>
      <CardHeader />
      <CardMedia
        image={img}
        title={title}
        sx={{ backgroundSize: 'contain', height: 250, marginTop: 3, marginBottom: 3 }}
      />
      <CardContent>
        <Typography variant="body1" component="div">
            {title}
        </Typography>
        <Typography variant="body2" color="text.secondary">
            {date}
        </Typography>
      </CardContent>
    </Card>
}

function App() {
  return (
    <div className="App" style={{width: '100%'}}>
        <Logo />
        <Introduction />
        <Stack spacing={4}>
            <Contact />
            <Typography variant="body1" style={{ marginBottom: '-25px', marginLeft: '15px' }}>Experience</Typography>
            <Experience title="Professional Cloud Architect" date="February 2023" img={Google} link='https://www.credential.net/138da4e3-f230-4af9-83e8-f559d99f8d0c' />
            <Experience title="Helicon" date="November 2022" img={Helicon} link='https://helicon.ai' />
        </Stack>
      {/*<OldStuff />*/}
        <BottomMenu>
            <LinkedIn />
            <Copyright />
        </BottomMenu>
    </div>
  );
}

export default App;
